@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Myeongjo:wght@400;700;800&display=swap);
body {
  margin: 0;
}


* {
    cursor: none !important;
}

.spinner-main {
    position: relative;
}

.main-main {
    position: absolute;
    margin: 0;
    z-index: -4;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.home-container {
    height: 100vh;
    width: 100vw;
    background: linear-gradient(180deg, rgba(210, 105, 58, 1) 35%, rgba(210, 239, 82, 1) 100%);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

}

.home-text-container {
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 400;
    font-size: 1.8rem;
    width:  50%;
}


.photo-container {
   /*  color: #103AAB; */
}

.nav-item-extra {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    color: rgb(0, 0, 0);
    font-family: 'Nanum Myeongjo', serif;
    font-weight: 700 !important;
    margin-right: 0px;
}

.nav-item-extra:hover {
    border: none !important;
}
.about-container {
    height: 100vh;
    width: 100%;
    background-color: #949494;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 400;
    font-size: 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
/*     overflow: hidden; */
}

.about-item-card {
    border: 2px solid black;
    padding: 2%;
    position: absolute;
}

.cv-link > a {
    color: black;
    text-decoration: none !important;
    font-family: 'Nanum Myeongjo', serif;
    font-weight: 700 !important;
    position: absolute;
    right: 15%;
    top: 70%;
}

.projects-container {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: #dbce54;
}

.grid-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin: 180px 0;
}
.project-card {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 600px;
    height: 600px;

}

.banner {
    position: absolute;
    /* border-radius: 50%;
    width: 420px; */
    width: 600px;
    height: 420px;
    font-size: 32px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-family: 'Josefin Sans', sans-serif;
    color: rgb(0, 0, 0);

}

.banner:hover {
    transition: opacity 1s ease-in-out;
    opacity: 0;
}

.banner:not( :hover ){ 
    transition: opacity 1.5s ease-in-out;
    opacity: 1;
}

.fa-icon {
    width: 25px;
    height: 25px;
    color: black;
/*     border: 2px solid black;
    border-radius: 50%;
    padding: 4px; */
}

.img-container {
    /* border-radius: 50%;
    width: 420px; */
    width: 600px;
    height: 420px;
    background-color: rgb(70, 70, 70);
    background-size: cover;
    background-position: center;
}

.info-container {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 100px;
    margin-top: 20px;
}

.text-container {
    width: 80%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.text-container > div:nth-of-type(1) {
    font-family: 'Josefin Sans', sans-serif;
    font-size: 28px;
    
}

.text-container > div:nth-of-type(3) {
    font-family: 'Nanum Myeongjo', serif;
    font-size: 14px;
    margin-top: 12px;
    font-weight: bold;
}

.text-container > div:nth-of-type(2) {
    font-family: 'Josefin Sans', sans-serif;
    font-size: 16px;
}

.links-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.navbar-container {
    width: 100%;
    font-family: 'Josefin Sans', sans-serif;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    position: fixed;
    z-index: 20;
    margin-top: 40px;
}

.nav-item {
    text-decoration: none;
    font-weight: 400;
    font-size: 1.2rem;
    color: black;
    margin: 20px 40px;
}

.nav-item:visited {
    color: BE2EFD;
}

.nav-item:hover{
    border-bottom: 2px solid black;
}

.active {
    border-bottom: 2px solid black;
}

.footer-container{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    position: fixed;
    bottom: 0px;
    background-color: #FEFEF3;
    font-family: 'Nanum Myeongjo', serif;
    font-weight: 700;
    height: 85px;
    z-index: 10;
}

.footer-container > *{
    margin: 35px 40px;
}

.bottom-nav {
    color: rgb(0, 0, 0);
    font-family: 'Nanum Myeongjo', serif;
    font-weight: 700;
    margin-right: 0px;
}

.footer-links-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.footer-links-container > div > a {
    text-decoration: none;
    color: black;
    margin-right: 14px;
}


.footer-links-container > div > a:visited {
    color: black;
}



.spinner-container {
    width: 100vw;
    height: 100vh;
    background-color: #FEFEF3;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    overflow: hidden;
}

.sun-element {
    position: absolute;
    width: 25vmin;
    height: 25vmin;
    border-radius: 50%;
    overflow: hidden;
    border: 4px solid black;
    /* clip-path: circle(40%); */
    background: linear-gradient(180deg, rgba(210, 105, 58, 1) 35%, rgba(210, 239, 82, 1) 100%);
    -webkit-animation : expand-bg 4s linear infinite;
    animation         : expand-bg 4s linear infinite;
}

.moon-element {
    position: relative;
    border-radius: 50%;
    background-color: black;
    width: 23vmin;
    height: 23vmin;
    overflow: hidden;
    -webkit-animation : left-to-right 4s linear infinite;
    animation         : left-to-right 4s linear infinite;
}

@-webkit-keyframes left-to-right {
    from {left: -600px;}
    30% {left: 0px;}
    40% {left: 0px;}
    65% {left: 70vw;}
    to {left: 70vw;}
}

@keyframes left-to-right {
    from {left: -600px;}
    30% {left: 0px;}
    40% {left: 0px;}
    65% {left: 70vw;}
    to {left: 70vw;}
}

@-webkit-keyframes expand-bg{
    from {    
            width: 25vmin;
            height: 25vmin;
    }

    65% { 
            width: 25vmin;
            height: 25vmin;
            border-radius: 50%;
    }
    
    to { 
            width: 200vmin;
            height: 200vmin;
            border-radius: 50%;
    }
}

@keyframes expand-bg{
    from {    
            width: 25vmin;
            height: 25vmin;
    }

    65% { 
            width: 25vmin;
            height: 25vmin;
            border-radius: 50%;
    }
    
    to { 
            width: 200vmin;
            height: 200vmin;
            border-radius: 50%;
    }
}

